.recommended {

  &__products {
    @include pageGrid;
    row-gap: 40px;
  }

  &__product {
    grid-column: 1/-1;
    @include onTablet {
      grid-column: 2/-2;
    }

    @include onDesktop {
      grid-column: span 4;
    }
  }

}

.product__photo {
  @include transPhoto;
}

.product__price{
  font-weight: bold;
}
